<template>
  <div class="main-box growth">
    <div class="main">
      <div class="middle">
        <div class="left">
          <live :name="gardenRecord.name"></live>
        </div>
        <div class="right">
          <p class="refresh">数据每隔五分钟自动刷新
            <button class="btn1" @click="refresh">
              <i class="iconfont">&#xe667;</i>刷新</button>
          </p>
          <ul class="ranking">
            <li>
              <h3 class="title">
                <em></em>以平均身高高低排名</h3>
              <div class="content">
                <el-line :xData="statureRankings.xData" :data="statureRankings.data" :color="statureRankings.color" :yName="statureRankings.yName" v-if="statureRankings.xData[0] !== '暂无数据'"></el-line>
                <no-data v-else :item="'平均身高'" />
              </div>
            </li>
            <li>
              <h3 class="title">
                <em></em>以平均体重高低排名</h3>
              <div class="content">
                <bars v-if="weightRankings.xData[0] !== '暂无数据'" :xData="weightRankings.xData" :data="weightRankings.data" :color="weightRankings.color" :yName="weightRankings.yName"></bars>
                <no-data v-else :item="'平均体重'" />
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="line"></div>
      <div class="bottom">
        <ul class="statistics">
          <li>
            <h3 class="title">
              <em></em>女童/男童BMI分布</h3>
            <ul class="content">
              <li>
                <pie v-if="girlData.data[0].name !== '暂无数据'" :data="girlData.data" :color="girlData.color" :title="girlData.title"></pie>
                <no-data v-else :item="'女童BMI'" />
              </li>
              <li>
                <pie v-if="boyData.data[0].name !== '暂无数据'" :data="boyData.data" :color="boyData.color" :title="boyData.title"></pie>
                <no-data v-else :item="'男童BMI'" />
              </li>
            </ul>
          </li>
          <li></li>
          <li>
            <h3 class="title">
              <em></em>幼儿园信息</h3>
            <div class="content">
              <matts :data="KindergartenData"></matts>
            </div>
          </li>
          <li>
            <h3 class="title">
              <em></em>教师信息</h3>
            <ul class="content">
              <li>
                <pie v-if="jobData.data[0].name !== '暂无数据'" :data="jobData.data" :color="jobData.color" :title="jobData.title" :pieSize="jobData.pieSize"></pie>
                <no-data v-else :item="'职称'" />
              </li>
              <li>
                <pie v-if="ageData.data[0].name !== '暂无数据'" :data="ageData.data" :color="ageData.color" :title="ageData.title" :pieSize="ageData.pieSize"></pie>
                <no-data v-else :item="'年龄'" />
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import ElMap from '@/common/echarts/Map'
import Live from '@/common/ezviz/Live'
import Matts from '@/pages/home/healthy/today/components/Matts'
import Pie from '@/common/echarts/Pie'
import ElLine from '@/common/echarts/Line'
import Bars from '@/common/echarts/expand/Bars'
import http from '@/common/axios/http'
import api from '@/common/axios/api'
export default {
  name: 'HealthyToday',
  components: {
    ElMap,
    Matts,
    Pie,
    ElLine,
    Bars,
    Live
  },
  data () {
    return {
      provinceId: localStorage.getItem('provinceId'),
      cityId: localStorage.getItem('cityId'),
      districtId: localStorage.getItem('districtId'),
      gardenName: localStorage.getItem('gardenName'),
      gardenId: localStorage.getItem('gardenId'),
      mapView: true,
      gardenRecord: {
        name: '拱墅区植材小学',
        location: '拱墅区'
      },
      // 以平均身高高低排名
      statureRankings: {
        xData: ['暂无数据'],
        data:
          [
            {
              name: '男童',
              value: [0]
            },
            {
              name: '女童',
              value: [0]
            }
          ],
        color: ['#53e9ee', '#ffe98f'],
        yName: '身高（cm）'
      },
      // 以平均身高高低排名
      weightRankings: {
        xData: ['暂无数据'],
        data:
          [
            {
              name: '男童',
              value: [0]
            },
            {
              name: '女童',
              value: [0]
            }
          ],
        yName: '体重（kg）'
      },
      // 各年级男童/女童BMI分布-女童
      girlData: {
        data: [
          { value: 0, name: '偏轻' },
          { value: 0, name: '均值' },
          { value: 0, name: '肥胖' },
          { value: 0, name: '超重' }
        ],
        color: ['#ff6c6c', '#ff9786', '#ffd05a', '#ffe98f'],
        title: '女童'
      },
      // 各年级男童/女童BMI分布-男童
      boyData: {
        data: [
          { value: 0, name: '偏轻' },
          { value: 0, name: '均值' },
          { value: 0, name: '肥胖' },
          { value: 0, name: '超重' }
        ],
        color: ['#315bff', '#7193ff', '#53e9ee', '#a4ffee'],
        title: '男童'
      },
      // 幼儿园信息
      KindergartenData: [
        {
          unit: '家',
          subText: '园所总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '园长总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在职教师总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在园儿童总数',
          sum: 0
        }
      ],
      // 职称分布
      jobData: {
        data: [
          { value: 0, name: '正高级' },
          { value: 0, name: '高级' },
          { value: 0, name: '一级' },
          { value: 0, name: '二级' },
          { value: 0, name: '三级' }
        ],
        color: ['#ffbc00', '#57d0ff', '#4d6bff', '#ff6562', '#9d70ff'],
        title: '职称分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      },
      // 年龄分布
      ageData: {
        data: [
          { value: 0, name: '00后' },
          { value: 0, name: '90后' },
          { value: 0, name: '80后' },
          { value: 0, name: '70后' }
        ],
        color: ['#1683fb', '#61ffe1', '#24cbff', '#7189ff'],
        title: '年龄分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      }
    }
  },
  methods: {
    // 平均身高，平均体重, BMI
    mapObiterData () {
      let firstDay = this.$moment().startOf('month').format("YYYY-MM-DD")
      let lastDay = this.$moment().format("YYYY-MM-DD")
      // 身高体重
      // const avgData = data2018-08-01 2018-08-31 hello
      const avgData = { gid: 1405, sdate: firstDay, edate: lastDay }
      window.$.ajax({
        url: 'http://api.youjiaoyun.net/weixin.asmx/GetGardenAvgDataList?jsoncallback=?',
        dataType: 'jsonp',
        data: avgData,
        jsonp: 'jsoncallback',
        async: false,
        success: (res) => {
          console.log(res,'123123123123123123')
          window.$('.refresh').find('i').removeClass('loading')
          const result1 = {
            className: [],
            height: {
              girl: [],
              boy: []
            }
          }
          const result2 = {
            className: [],
            weight: {
              girl: [],
              boy: []
            }
          }
          let $data = []
          if (res.length) {
            for (let i = 0, len = res.length; i < len; i++) {
              // 女高, 女重, 男高, 男重
              let sum = [[], [], [], []]
              for (let j = 0, len = res[i].classdatas.length; j < len; j++) {
                if (res[i].classdatas[j].datas.length) {
                  sum[0].push(res[i].classdatas[j].datas[0].ClassAvgHeight)
                  sum[1].push(res[i].classdatas[j].datas[0].ClassAvgWeight)
                  sum[2].push(res[i].classdatas[j].datas[1].ClassAvgHeight)
                  sum[3].push(res[i].classdatas[j].datas[1].ClassAvgWeight)
                }
              }
              sum = sum.map((p) => {
                let pSum = 0
                if (p.length) {
                  for (let i = 0, len = p.length; i < len; i++) {
                    pSum += p[i]
                  }
                  return pSum / p.length
                } else {
                  return 0
                }
              })
              $data.push({
                name: res[i].GradeName,
                girl: {
                  height: sum[0],
                  weight: sum[1]
                },
                boy: {
                  height: sum[2],
                  weight: sum[3]
                }
              })
            }
            console.log($data,'what')
            const newData = $data.filter((item) => {
              return item.name && item.girl.height && item.boy.height
            })
            console.log(newData)
            if (newData.length) {
              newData.sort((p1, p2) => {
                return (p2.boy.height + p2.girl.height) - (p1.boy.height + p1.girl.height)
              })
              for (let i = 0; i < newData.length; i++) {
                result1.className.push(newData[i].name)
                result1.height.girl.push(newData[i].girl.height)
                result1.height.boy.push(newData[i].boy.height)
              }
              this.statureRankings = {
                xData: result1.className,
                data:
                  [
                    {
                      name: '男童',
                      value: result1.height.boy
                    },
                    {
                      name: '女童',
                      value: result1.height.girl
                    }
                  ],
                color: ['#53e9ee', '#ffe98f'],
                yName: '身高（cm）'
              }
            } else {
              this.statureRankings = {
                xData: ['暂无数据'],
                data:
                  [
                    {
                      value: ['0']
                    },
                    {
                      value: ['0']
                    }
                  ]
              }
            }
            const newDataWeight = $data.filter((item) => {
              return item.name && item.girl.weight && item.boy.weight
            })
            if (newDataWeight.length) {
              newDataWeight.sort((p1, p2) => {
                return (p2.boy.weight + p2.girl.weight) - (p1.boy.weight + p1.girl.weight)
              })
              for (let i = 0; i < newDataWeight.length; i++) {
                result2.className.push(newDataWeight[i].name)
                result2.weight.girl.push(newDataWeight[i].girl.weight)
                result2.weight.boy.push(newDataWeight[i].boy.weight)
              }
              this.weightRankings = {
                xData: result2.className,
                data:
                  [
                    {
                      name: '男童',
                      value: result2.weight.boy
                    },
                    {
                      name: '女童',
                      value: result2.weight.girl
                    }
                  ],
                color: ['#53e9ee', '#ffe98f'],
                yName: '体重（kg）'
              }
            } else {
              this.weightRankings = {
                xData: ['暂无数据'],
                data:
                  [
                    {
                      value: ['0']
                    },
                    {
                      value: ['0']
                    }
                  ],
              }
            }
          } else {
            this.weightRankings = {
              xData: ['暂无数据'],
              data:
                [
                  {
                    value: ['0']
                  },
                  {
                    value: ['0']
                  }
                ]
            },
              this.statureRankings = {
                xData: ['暂无数据'],
                data:
                  [
                    {
                      value: ['0']
                    },
                    {
                      value: ['0']
                    }
                  ],
              }
          }
        },
        error: function (err) {
          console.log(err)
        }
      })
      // BMI
      let data = { ProvinceId: this.provinceId, CityId: this.cityId, DistrictId: this.districtId, Gid: this.gardenId, IsMonth: true, StartDate: firstDay, EndDate: lastDay }
      window.$.ajax({
        url: 'http://api.youjiaoyun.net/weixin.asmx/GetBodyCheckResultList?jsoncallback=?',
        dataType: 'jsonp',
        data: data,
        jsonp: 'jsoncallback',
        async: true,
        success: (res) => {
          // 女童
          this.girlData.data = this.avgHW(res, 0, 7006)
          // 男童
          this.boyData.data = this.avgHW(res, 1, 7006)
          console.log(this.girlData, this.boyData)
        },
        error: function (err) {
          console.log(err)
        }
      })
    },
    avgHW (res, sex, gid) {
      let arr = []
      let BMI = res.filter((item) => {
        return item.Gender === sex && item.Gid === gid
      })
      BMI = this.$common.compare(BMI, 'BMIResult')
      if (Object.keys(BMI).length !== 0) {
        const defaultVal = {
          '偏轻': 0,
          '均值': 0,
          '肥胖': 0,
          '偏重': 0,
          '超重': 0
        }
        for (let key in defaultVal) {
          BMI[key] ? defaultVal[key] = BMI[key] : defaultVal[key] = 0
        }
        window.$.each(defaultVal, (key, value) => {
          arr.push({
            name: key,
            value: value
          })
        })
      } else {
        arr = [{ name: '暂无数据', value: 0 }]
      }
      return arr
    },
    // 获取该地区id
    getAreaId (name) {
      return this.$store.state.areaIdList.filter((item) => {
        return item.ProvinceName === name || item.CityName === name || item.DistrictName === name
      })
    },
    // 刷新
    refresh () {
      window.$('.refresh').find('i').addClass('loading')
      this.mapObiterData()
    },
    // 幼儿园，教师信息
     getGardenInfos: async function () {
      const res = await http.get(api.getGardenInfos, {
        gardenId: this.gardenId
      })
      let d = res.data.data
      console.log(d, '123123123123123123123')
      this.KindergartenData[0].sum = d.gardenCount
      this.KindergartenData[1].sum = d.kindergartenCount
      this.KindergartenData[2].sum = d.teacherCount
      this.KindergartenData[3].sum = d.totalCount
      const ages = ['00', '90', '80', '70']
      const jobs = ['正高级', '高级', '一级', '二级', '三级']
      this.jobData.data = [
        { value: 0, name: '正高级' },
        { value: 0, name: '高级' },
        { value: 0, name: '一级' },
        { value: 0, name: '二级' },
        { value: 0, name: '三级' }
      ]
      this.ageData.data = [
        { value: 0, name: '00后' },
        { value: 0, name: '90后' },
        { value: 0, name: '80后' },
        { value: 0, name: '70后' }
      ]
      if (d.jobs.length) {
        for (let i = 0, len = this.jobData.data.length; i < len; i++) {
          const result = d.jobs.filter((item) => {
            return item.item1 === jobs[i]
          })
          result.length ? this.jobData.data[i].value = result[0].item2 : this.jobData.data[i].value = 0
        }      
      } else {
        this.jobData.data = [{ name: '暂无数据', value: 0 }]
      }
      if (d.ages.length) {
        for (let i = 0, len = this.ageData.data.length; i < len; i++) {
          const result = d.ages.filter((item) => {
            // return item.item1 === ages[i]
            return item.item1.substring(0, 1) === ages[i].substring(0, 1)
          })
          result.length ? this.ageData.data[i].value = result.length : this.ageData.data[i].value = 0
        }
      } else {
        this.ageData.data = [{ name: '暂无数据', value: 0 }]
      }
    },
    gardenView (params) {
      this.mapView = !this.mapView
      this.gardenRecord = {
        name: params.name,
        location: params.location
      }
    },
    toMapView () {
      this.mapView = !this.mapView
    }
  },
  mounted () {
    // 初始化所有边框样式
    this.$common.border(window.$('.bottom .content'))
    this.mapObiterData()
    this.getGardenInfos()
  }
}
</script>

<style lang="stylus" scoped>
.growth
  .main
    .bottom
      .statistics > li:nth-child(1)
        flex 0 0 50%
        .content
          li
            float left
            width 50%
            height 100%
            padding 20px 10px
            box-sizing border-box
      .statistics > li:nth-child(2)
        display none
</style>
<style scoped>
.handle-no-data {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>



